

// Location List Styling

.location-list{
	list-style-type:none;
	margin:0;
	padding:0;

	li:not(:last-of-type){
		margin-bottom:$gap;
	}
	li{
		background: $location-bg;
		clear:both;
		height:120px;
		position:relative;
	}
}

.location{
	
	//position:relative;
	//clear:both;
	float:left;

	&__image{
		//width:auto;
		//max-height:120px;
		height:100%;
		width:auto;
		float:left;
		height:120px;
	}

	&__body{
		padding: $gap;
		float:left;
		word-wrap: break-word;

	}

	&__name{
		margin:0;
	}

	&__description{
		margin:0;
	}

	&__user-count{
		position:absolute;
		right:1em;
		top:50%;
		transform: translateY(-50%);
		font-size:3em;
		color:#60AB71;
	}

	&__cta{
		display:inline-block;
		margin-top:$gap;
		background:#60AB71;
		padding:$gap/2;
		color:white;
		text-decoration: none;
		border-radius: 3px;
		cursor:pointer;
	}

}

